<template>
    <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="运营报表" name="first">
            <div class="p_6">
                <div class="search-wrapper">
                    <div class="search-content">
                        <el-select v-model="searchParam.device" slot="prepend" placeholder="手机设备" style="width: 120px;">
                            <el-option
                                v-for="item in deviceOptions"
                                :key="item.value"
                                :label="item.name"
                                :value="item.value">
                            </el-option>
                        </el-select>
                        <el-date-picker
                            v-model="searchParam.dateRange"
                            type="daterange"
                            placeholder="选择日期范围">
                        </el-date-picker>
                        <!--<el-time-select-->
                        <!--v-model="value1"-->
                        <!--:picker-options="{-->
                        <!--start: '00:00',-->
                        <!--step: '01:00',-->
                        <!--end: '24:00'-->
                        <!--}"-->
                        <!--placeholder="选择时间">-->
                        <!--</el-time-select>-->
                    </div>
                    <div class="search-btn">
                        <el-button slot="append" icon="search" @click="search" style="width:80px; ">查询</el-button>
                    </div>
                </div>
                <el-table
                    :data="tableData" v-loading.body="listLoading" element-loading-text="拼命加载中" style="width: 100%" border
                    :default-sort="{prop: 'time', order: 'descending'}">
                    <el-table-column
                        type="index"
                        align="center"
                        width="50">
                    </el-table-column>
                    <el-table-column prop="date" label="日期" align="center" width="115" sortable></el-table-column>
                    <el-table-column prop="platform" label="设备" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="reg_num" label="注册用户" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="loan_num" label="借款人申请" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="Fpid_info" label="身份证扫描" align="center" width="80"
                                     sortable></el-table-column>
                    <el-table-column prop="Fimg_face" label="人脸识别" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="Fbase_info" label="基本信息" align="center" width="80"
                                     sortable></el-table-column>
                    <el-table-column prop="Fjob_info" label="职业信息" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="Flink_info" label="联系人信息" align="center" width="80"
                                     sortable></el-table-column>
                    <el-table-column prop="Fphone_info" label="运营商验证" align="center" width="80"
                                     sortable></el-table-column>
                    <el-table-column prop="Fband_bank" label="银行卡信息" align="center" width="80"
                                     sortable></el-table-column>
                    <el-table-column prop="Ftaobao_info" label="淘宝" align="center" width="80"
                                     sortable></el-table-column>
                    <el-table-column prop="Femail_info" label="邮箱" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="auditing" label="审核中" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="auditSuccess" label="审核成功" align="center" width="80"
                                     sortable></el-table-column>
                    <el-table-column prop="auditFail" label="审核失败" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="loaning" label="放款中" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="loanSuccess" label="放款成功" align="center" width="80"
                                     sortable></el-table-column>
                    <el-table-column prop="loanFail" label="放款失败" align="center" width="80" sortable></el-table-column>

                </el-table>
                <div class="pagination_position">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="pageSizes"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total">
                    </el-pagination>
                </div>
            </div>
        </el-tab-pane>
        <div v-if="showDetail">
            <el-tab-pane name="second">
                <span slot="label"> 详情页<i class="el-icon-close" @click="removeLabel($event)"></i></span>
            </el-tab-pane>
        </div>
    </el-tabs>
</template>

<style scoped>
    .el-select .el-input {
        width: 100px;
    }

    date-range:after {
        display: block;
        content: '.';
        line-height: 1;
        clear: both;
        visibility: hidden;
    }

    .pagination_position {
        padding-top: 20px;
    }

    .echarts {
        height: 300px;
    }
</style>
<style>
    .el-table .cell, .el-table th > div {
        padding-left: 5px;
        padding-right: 5px;
        box-sizing: border-box;
        text-overflow: ellipsis;
    }
</style>
<script>
    import Common from '../../../utils/common'
    import {Notification} from 'element-ui';
    import formatDate from '../../../utils/formatDate.js'
    export default{
        data(){
            let date = new Date();
            let prevMonth = new Date(date.setDate(date.getDate() - 30));
            //let prevMonth = new Date(date.setDate(date.getMonth() - 1));
            let now = new Date();

            return {
                deviceOptions: [
                    {
                        value: 'All',
                        name: 'All',
                    }, {
                        value: 'iOS',
                        name: 'iOS',
                    }, {
                        value: 'Android',
                        name: 'Android'
                    }
                ],
                detailData: {},
                searchParam: {
                    device: "All",
                    dateRange: [prevMonth, now],
                    time: '',
                },
                activeName: 'first',
                showDetail: false,
                name: 'detail',
                total: 0,
                currentPage: 1,
                pageSizes: [10, 20, 50, 100, 200],
                pageSize: 50,
                tableData: [],
                select: "",
                listLoading: true,
            }
        },
        methods: {
            getDevices(){
                Common.ajax({
                    url: '/web/v1/options/devices',
                    type: 'POST',
                    contentType: 'application/json',
                    success: (response) => {
                        if (response.data && response.data.list) {
                            this.deviceOptions = response.data.list;
                        }
                    },
                    error: (err) => {
                        this.deviceOptions = [{
                            value: 'All',
                            name: 'All',
                        }, {
                            value: 'iOS',
                            name: 'iOS',
                        }, {
                            value: 'Android',
                            name: 'Android'
                        }];
                    }
                });
            },
            search(){
                let self = this;
                let dateRange = self.searchParam.dateRange;
                this.listLoading = true;
                //console.log(formatDate(dateRange[0], 'yyyy-MM-dd'));
                Common.ajax({
                    url: "/web/v1/operation/conversionRateByDay",
                    type: 'POST',
                    contentType: "application/json",
                    data: {
                        page: self.currentPage,
                        pageSize: self.pageSize,
                        device: self.searchParam.device,
                        beginTime: formatDate(dateRange[0], 'yyyy-MM-dd') + ' 00:00:00',
                        endTime: formatDate(dateRange[1], 'yyyy-MM-dd') + ' 23:59:59',
                    },
                    success(response){
                        self.listLoading = false;
                        //console.log(response);
                        if (response.data && response.data.list) {
                            let data = response.data;
                            self.tableData = data.list;
                            self.total = data.pageUtil.count;
                        }
                    },
                    error(err){
                        self.listLoading = false;
                        let error = err.message || err;
                        Notification.error({
                            title: '错误',
                            message: error
                        });
                    }
                })
            },
            removeLabel(event){
                //console.log(event);
                event.stopPropagation();
                this.activeName = 'first';
                this.showDetail = false;

            },
            handleSizeChange(val) {
                this.pageSize = val;
                //console.log(`每页 ${val} 条`);
                this.search();
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                //console.log(`当前页: ${val}`);
                this.search();
            },
            handleClick(tab, event) {
                let index = tab.index;
                if (index == 0) {
                    this.activeName = 'first';
                }
            }
        },
        created(){
            //this.search();
            //console.log('created', this);
        },
        mounted(){
            //
            //console.log('mounted', this);
            this.getDevices();
            this.search();
        }
    }
</script>
