e-column>\r\n                    <el-table-column prop=\"Flink_info\" label=\"联系人信息\" align=\"center\" width=\"80\"\r\n                                     sortable></el-table-column>\r\n                    <el-table-column prop=\"Fphone_info\" label=\"运营商验证\" align=\"center\" width=\"80\"\r\n                                     sortable></el-table-column>\r\n                    <el-table-column prop=\"Fband_bank\" label=\"银行卡信息\" align=\"center\" width=\"80\"\r\n                                     sortable></el-table-column>\r\n                    <el-table-column prop=\"Ftaobao_info\" label=\"淘宝\" align=\"center\" width=\"80\"\r\n                                     sortable></el-table-column>\r\n                    <el-table-column prop=\"Femail_info\" label=\"邮箱\" align=\"center\" width=\"80\" sortable></el-table-column>\r\n                    <el-table-column prop=\"auditing\" label=\"审核中\" align=\"center\" width=\"80\" sortable></el-table-column>\r\n                    <el-table-column prop=\"auditSuccess\" label=\"审核成功\" align=\"center\" width=\"80\"\r\n                                     sortable></el-table-column>\r\n                    <el-table-column prop=\"auditFail\" label=\"审核失败\" align=\"center\" width=\"80\" sortable></el-table-column>\r\n                    <el-table-column prop=\"loaning\" label=\"放款中\" align=\"center\" width=\"80\" sortable></el-table-column>\r\n                    <el-table-column prop=\"loanSuccess\" label=\"放款成功\" align=\"center\" width=\"80\"\r\n                                     sortable></el-table-column>\r\n                    <el-table-column prop=\"loanFail\" label=\"放款失败\" align=\"center\" width=\"80\" sortable></el-table-column>\r\n\r\n                </el-table>\r\n                <div class=\"pagination_position\">\r\n                    <el-pagination\r\n                        @size-change=\"handleSizeChange\"\r\n                        @current-change=\"handleCurrentChange\"\r\n                        :current-page=\"currentPage\"\r\n                        :page-sizes=\"pageSizes\"\r\n                        :page-size=\"pageSize\"\r\n                        layout=\"total, sizes, prev, pager, next, jumper\"\r\n                        :total=\"total\">\r\n                    </el-pagination>\r\n                </div>\r\n            </div>\r\n        </el-tab-pane>\r\n        <div v-if=\"showDetail\">\r\n            <el-tab-pane name=\"second\">\r\n                <span slot=\"label\"> 详情页<i class=\"el-icon-close\" @click=\"removeLabel($event)\"></i></span>\r\n            </el-tab-pane>\r\n        </div>\r\n    </el-tabs>\r\n</template>\r\n\r\n<style scoped>\r\n    .el-select .el-input {\r\n        width: 100px;\r\n    }\r\n\r\n    date-range:after {\r\n        display: block;\r\n        content: '.';\r\n        line-height: 1;\r\n        clear: both;\r\n        visibility: hidden;\r\n    }\r\n\r\n    .pagination_position {\r\n        padding-top: 20px;\r\n    }\r\n\r\n    .echarts {\r\n        height: 300px;\r\n    }\r\n</style>\r\n<style>\r\n    .el-table .cell, .el-table th > div {\r\n        padding-left: 5px;\r\n        padding-right: 5px;\r\n        box-sizing: border-box;\r\n        text-overflow: ellipsis;\r\n    }\r\n</style>\r\n<script>\r\n    import Common from '../../../utils/common'\r\n    import {Notification} from 'element-ui';\r\n    import formatDate from '../../../utils/formatDate.js'\r\n    export default{\r\n        data(){\r\n            let date = new Date();\r\n            let prevMonth = new Date(date.setDate(date.getDate() - 30));\r\n            //let prevMonth = new Date(date.setDate(date.getMonth() - 1));\r\n            let now = new Date();\r\n\r\n            return {\r\n                deviceOptions: [\r\n                    {\r\n                        value: 'All',\r\n                        name: 'All',\r\n                    }, {\r\n                        value: 'iOS',\r\n                        name: 'iOS',\r\n                    }, {\r\n                        value: 'Android',\r\n                        name: 'Android'\r\n                    }\r\n                ],\r\n                detailData: {},\r\n                searchParam: {\r\n                    device: \"All\",\r\n                    dateRange: [prevMonth, now],\r\n                    time: '',\r\n                },\r\n                activeName: 'first',\r\n                showDetail: false,\r\n                name: 'detail',\r\n                total: 0,\r\n                currentPage: 1,\r\n                pageSizes: [10, 20, 50, 100, 200],\r\n                pageSize: 50,\r\n                tableData: [],\r\n                select: \"\",\r\n                listLoading: true,\r\n            }\r\n        },\r\n        methods: {\r\n            getDevices(){\r\n                Common.ajax({\r\n                    url: '/web/v1/options/devices',\r\n                    type: 'POST',\r\n                    contentType: 'application/json',\r\n                    success: (response) => {\r\n                        if (response.data && response.data.list) {\r\n                            this.deviceOptions = response.data.list;\r\n                        }\r\n                    },\r\n                    error: (err) => {\r\n                        this.deviceOptions = [{\r\n                            value: 'All',\r\n                            name: 'All',\r\n                        }, {\r\n                            value: 'iOS',\r\n                            name: 'iOS',\r\n                        }, {\r\n                            value: 'Android',\r\n                            name: 'Android'\r\n                        }];\r\n                    }\r\n                });\r\n            },\r\n            search(){\r\n                let self = this;\r\n                let dateRange = self.searchParam.dateRange;\r\n                this.listLoading = true;\r\n                //console.log(formatDate(dateRange[0], 'yyyy-MM-dd'));\r\n                Common.ajax({\r\n                    url: \"/web/v1/operation/conversionRateByDay\",\r\n                    type: 'POST',\r\n                    contentType: \"application/json\",\r\n                    data: {\r\n                        page: self.currentPage,\r\n                        pageSize: self.pageSize,\r\n                        device: self.searchParam.device,\r\n                        beginTime: formatDate(dateRange[0], 'yyyy-MM-dd') + ' 00:00:00',\r\n                        endTime: formatDate(dateRange[1], 'yyyy-MM-dd') + ' 23:59:59',\r\n                    },\r\n                    success(response){\r\n                        self.listLoading = false;\r\n                        //console.log(response);\r\n                        if (response.data && response.data.list) {\r\n                            let data = response.data;\r\n                            self.tableData = data.list;\r\n                            self.total = data.pageUtil.count;\r\n                        }\r\n                    },\r\n                    error(err){\r\n                        self.listLoading = false;\r\n                        let error = err.message || err;\r\n                        Notification.error({\r\n                            title: '错误',\r\n                            message: error\r\n                        });\r\n                    }\r\n                })\r\n            },\r\n            removeLabel(event){\r\n                //console.log(event);\r\n                event.stopPropagation();\r\n                this.activeName = 'first';\r\n                this.showDetail = false;\r\n\r\n            },\r\n            handleSizeChange(val) {\r\n                this.pageSize = val;\r\n                //console.log(`每页 ${val} 条`);\r\n                this.search();\r\n            },\r\n            handleCurrentChange(val) {\r\n                this.currentPage = val;\r\n                //console.log(`当前页: ${val}`);\r\n                this.search();\r\n            },\r\n            handleClick(tab, event) {\r\n                let index = tab.index;\r\n                if (index == 0) {\r\n                    this.activeName = 'first';\r\n                }\r\n            }\r\n        },\r\n        created(){\r\n            //this.search();\r\n            //console.log('created', this);\r\n        },\r\n        mounted(){\r\n            //\r\n            //console.log('mounted', this);\r\n            this.getDevices();\r\n            this.search();\r\n        }\r\n    }\r\n</script>\r\n"]}]);
